body {
  font-family: 'Slabo 27px', serif;
  overflow: hidden;
}

.wrapper {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 50px calc(100vh - 50px);
  overflow: auto;
  position: relative;
}

header,
.main {
  grid-column: 1;
}

header ul {
  background-color: #7c7fbc;
  list-style: none;
  margin: 0;
  padding: 0;
}

header li {
  display: inline-block;
}

header a {
  color: #fff;
  display: block;
  line-height: 50px;
  padding-left: 15px;
  padding-right: 15px;
  text-decoration: none;
}

header a:hover {
  background-color: #444;
  color: #fff;
}

.main {
  background-color: rgba(255, 255, 255, 0.7);
  padding: 0 10px;
}

a {
  color: #7c7fbc;
}

.articles {
  margin: 3rem 0;
  padding: 0;
}

.articles a {
  display: block;
  text-decoration: none;
}

.articles article {
  font-weight: bold;
  font-size: 1.17em;
  margin-bottom: 15px;
  position: relative;
}

.articles a:hover {
  color: #333;
}

.article-date {
  color: #444;
  display: block;
  font-size: 11px;
  font-weight: 900;
}

.articles .article-title {
  text-decoration: underline;
}

.loading {
  font-size: 1rem;
  line-height: calc(100vh - 50px);
  text-align: center;
}

.article {
  margin: 3rem 0 2rem;
}

.article h1 {
  margin-top: 0;
}

blockquote {
  border-left: 4px solid #eee;
  margin: 0;
  padding: 3px 0 3px 15px;
}

/* only for inline code */
code:not(.hljs) {
  background-color: #eee;
  border-radius: 4px;
  padding: 1px 4px;
}

@media (min-width: 800px) {
  .wrapper {
    grid-template-columns: 25px 800px auto;
    grid-template-rows: 50px calc(100vh - 50px);
  }

  header {
    grid-column: 1 / span 3;
  }

  header ul {
    display: grid;
    grid-template-columns: 1fr auto auto auto;
  }

  header li:first-child a {
    display: inline-block;
  }

  .main {
    grid-column: 2 / span 1;
  }

  .main {
    padding: 0;
  }

  .articles article a {
    border-left: 2px solid #eee;
    padding: 3px 0 3px 15px;
  }

  .articles article a:hover {
    border-color: orange;
  }
}